*{
    margin: 0;
    padding: 0;
}
.imgstyle div{
    width: 45%;
    height: 250px;
    margin-left: 3.5%;
    display: inline-block;
    margin-top: 3%;
}
.imgstyle img{
    display: inline-block;
    width: 100%;
    height: 200px;
    border-radius: 3%;
}
.imgstyle .decription{
    background-color: #fff;
    color: black;
    font-size: 15px;
}
.imgstyle .price{
    background-color: #fff;
    font-size: 15px;
    color: red;
}